import React from 'react'
import Container from '../../components/Container'
import {
  Chart as ChartJS,
  Filler,
  Legend,
  LineElement,
  PointElement,
  RadialLinearScale,
  Tooltip,
} from 'chart.js'
import { Radar } from 'react-chartjs-2'
import * as s from './styles'
import CabinetImg from '../../assets/cabinet.png'

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
)

const labels = [
  [' ', ' '],
  [' ', ' '],
  [' ', ''],
  ['  ', ''],
  [' '],
  ['  ', ' ', ''],
  [' ', ' ', '  ']
]

const resultArr = []
for (let i = 0; i < 7; i++) {
  resultArr.push(Math.ceil(Math.random() * 100))
}

export const Cabinet = () => {
  const resultData = [35, 56, 0, 67, 76, 43, 100]

  const data = {
    labels,
    datasets: [
      {
        label: '%  ',
        data: resultArr,
        backgroundColor: 'transparent',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 6,
      }
    ],
  }

  const options = {
    responsive: true,
    tooltip: {
      enabled: true,
    },
    layout: {
      beginAtZero: true,
      padding: {
        right: 35,
        bottom: 60
      }
    },
    plugins: {
      // legend: {
      //   display: false
      // }
      datalabels: {
        formatter: function (value, context) {
          console.log(value)
          return context.chart.data.labels[context.dataIndex]
        }
      }
    },
    scales: {
      r: {
        min: 0,
        max: 100,
        gridLines: {
          circular: false,
        },
        ticks: {
          callback: (value) => {
            return `${value}%`
          },
        },
        angleLines: {
          color: 'rgba(14,103,179,0.53)',
        },
        grid: {
          color: '#005392',
        },
        pointLabels: {
          maxWidth: 100,
          padding: 10,
          font: {
            size: 14,
          },
          color: '#081828',
          callback(value, i) {
            return [...value, `${resultArr[i]}%`]
          }
        }
      }
    },
  }

  return (
    <Container>
      <img alt="cabinet" src={CabinetImg} className={s.image} />
      <s.Table>
        <s.Thead>
          <tr>
            <s.Td colSpan="3">  </s.Td>
          </tr>
        </s.Thead>
        <tbody>
        <tr>
          <s.Td> :</s.Td>
          <s.Td>:</s.Td>
          <s.Td>:</s.Td>
        </tr>
        <tr>
          <s.Td>   </s.Td>
          <s.Td>{resultData[0]}%</s.Td>
          <s.Td>
                .     
              ,        .
                 
          </s.Td>
        </tr>
        <tr>
          <s.Td>   </s.Td>
          <s.Td>{resultData[1]}%</s.Td>
          <s.Td>
                ,    
                   , 
               .       
            
          </s.Td>
        </tr>
        <tr>
          <s.Td>  </s.Td>
          <s.Td>{resultData[2]}%</s.Td>
          <s.Td>
                 ,  
          </s.Td>
        </tr>
        <tr>
          <s.Td>   </s.Td>
          <s.Td>{resultData[3]}%</s.Td>
          <s.Td>
                ,    
                   , 
               .       
            
          </s.Td>
        </tr>
        <tr>
          <s.Td> </s.Td>
          <s.Td>{resultData[4]}%</s.Td>
          <s.Td>
                     .
                  .  
                .   
             ,       
          </s.Td>
        </tr>
        <tr>
          <s.Td>     </s.Td>
          <s.Td>{resultData[5]}%</s.Td>
          <s.Td>
                .     
              ,        .
                 
          </s.Td>
        </tr>
        <tr>
          <s.Td>      </s.Td>
          <s.Td>{resultData[6]}%</s.Td>
          <s.Td>
                     .
                  .  
                .   
             ,       
          </s.Td>
        </tr>
        </tbody>
      </s.Table>
      <s.ChartWrapper>
        <Radar type="radar" data={data} options={options} />
      </s.ChartWrapper>
    </Container>
  )
}
